html {
  scroll-behavior: smooth;
}

@media (prefers-reduced-motion: reduce) {
  html {
      scroll-behavior: auto;
  }
}

body { 
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  background-color: #fafafa;
}

h3 {
  font-family: 'Playfair Display', serif;
}

.navbar {
  background-color: #fafafa !important;
}

.navbar-toggler-icon {
  color: #191919 !important;
}

.navbar-nav {
  position: absolute;
  right: 20px;
}

.navbar a {
  color:  #989f7a !important;
  text-decoration: none;
}

.navbar a:hover {
  color:  #191919 !important;
}

.hero h1 {
  margin-top: 200px;
  text-align: center;
}

.hero h3 {
  text-align: center;
}

.hero-waves {
  width: 100%;
}

.about h3 {
  text-align: center;
}

.about img {
  border-radius: 100px;
  border: solid 10px #989f7a;
  margin: 20px;
  margin-left: 50px;
  width: 475px;
}

@media only screen and (max-width: 500px) {
  .about img {
      display: block;
      margin: 0 auto;
      width: 400px;
  }
}

.about p {
  margin: 20px;
  padding: 20px;
}

.skills h3 {
  text-align: center;
}

.skills h4 {
  margin: 20px;
  padding: 20px;
}

.skills p {
  display: inline-flex;
  margin: 20px;
  padding: 20px;
  border-radius: 75px;
  box-shadow: 0 8px 6px -6px black;
}

.skills p:hover {
  cursor: pointer;
  transition: all 0.3s linear;
  -webkit-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  -moz-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  -o-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
}

#front-end p {
  background-color: #82B1AD;
}

#back-end p {
  background-color: #989f7a;
}

#workflow p {
  background-color: #D7B590;
}

#special p {
  color: #fafafa;
  background-color: #BA5354;
}

.projects-display h3{
  margin-top: 40px;
  text-align: center;
}

.project-cards {
  display: inline-flex;
  padding: 20px;
  margin: 0 auto;
}

@media only screen and (max-width: 975px) {
  .project-cards {
      display: inline-block;
      margin: 0 auto;
  }
}

.btn {
  background-color: #989f7a;
  color: #fafafa;
}

.card-body a {
  color: #fafafa;
}

.card-body a:hover {
  transition: all 0.3s linear;
  -webkit-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  -moz-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  -o-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
}

.contact {
  background-color: #82B1AD;
  margin-top: 20px;
}

.contact h3 {
  text-align: center;
  padding: 50px;
}

.contact-wrapper {
  align-items: center;
  display: flex;
  justify-content: center;
}

.contact a {
  color: #fafafa;
}

.contact a:hover {
  text-decoration: none;
}

.contact-wrapper img {
  display: block;
  margin-top: 20px;
  padding: 5px;
  margin-bottom: 50px;
}

.contact-icons {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 2.5rem;
  padding: 5px;
}

.img-credit {
  font-size: 0.5em;
}

#return-img {
  text-align: center;
  display: block;
  padding: 10px;
  color: #191919;
}

footer {
  background-color: #191919;
}

footer p {
  color: #fafafa;
  font-size: 0.75em;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 0;
}